/*==================================================
  Switches - Created with Pure CSS, no js required
==================================================== */
.switch {
  color: #f85926;
}

.switch label {
  position: relative;
  display: inline-block;
  border: 1px solid #d6d6d6;
  border-radius: 60px;
  color: #6b6b6b;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 15px;
  line-height: 20px;
  text-align: center;
  background: #ddd;
  width: 85px;
  height: 32px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.switch label:after {
  content: "";
  display: block;
  background: #fff;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 41px;
  height: 30px;
  padding: 5px;
  text-align: center;
  border-radius: 60px;
  transition: 0.3s ease-in-out;
}

.switch input {
  display: none;
}

.switch input + label {
  margin-left: 0;
  margin-right: 0;
}

.switch input:checked + label {
  background: #fa541d;
}

.switch input:checked + label:after {
  left: 42px;
}

/*==================================================================
  Switches
===================================================================*/
.switch--yes-no {
  cursor: pointer;
  position: relative;
  display: inline-block;
}

.switch--yes-no > label {
  cursor: pointer;
}

.switch--yes-no > label:before {
  content: attr(data-on);
  position: absolute;
  top: 6px;
