.preloader-wrap .box {
  background: #FA5A28 url(../img/bg-login.png) repeat;
  top: 0;
  left: 0;
  z-index: 499;
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
}

.loader3 {
  position: relative;
  width: 150px;
  height: 80px;
  top: 49%;
  left: 49%;
  background: url(../img/preloader_2.png) no-repeat top left;
  background-size: 45px 45px;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  filter: alpha(opacity=50);
  -moz-opacity: 0.5;
  -khtml-opacity: 0.5;
  opacity: 0.5;
}

.loader4 {
  content: "";
  position: absolute;
  background-color: #fff;
  background: url(../img/preloader_2.png) no-repeat top left;
  top: 49%;
  left: 49%;
  width: 150px;
  height: 80px;
  z-index: 0;
  opacity: 1;
  transform-origin: 100% 0%;
  animation: loader3 10s ease-in-out infinite;
  background-size: 45px 45px;
}

@-webkit-keyframes loader3 {
  0% {
    width: 0;
  }
  70% {
    width: 150px;
    opacity: 1;
  }
  90% {
    opacity: 0;
    width: 150px;
  }
  100% {
    opacity: 0;
    width: 0;
  }
}

@keyframes loader3 {
  0% {
    width: 0;
  }
  70% {
    width: 150px;
    opacity: 1;
  }
  90% {
    opacity: 0;
    width: 150px;
  }
  100% {
    opacity: 0;
    width: 0;
  }
}

