/*==================================================
  Radios and Checkboxes
  ==================================================== */
  .radio-custom,
  .checkbox-custom {
    display: inline-block;
    background-color: transparent;
    color: inherit;
  }
  /* hide default input */
  .radio-custom input,
  .checkbox-custom input {
    display: none;
  }
  .checkbox-custom label:empty,
  .radio-custom label:empty {
    padding-left: 10px;
  }
  /* radio container */
  .radio-custom label {
    cursor: pointer;
    position: relative;
    padding-left: 32px;
    margin-right: 15px;
    margin-bottom: 5px;
    font-weight: normal;
  }
  .radio-custom.radio-disabled label {
    color: #CCC;
  }
  /* radio border */
  .radio-custom label:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 64px;
    width: 20px;
    height: 20px;
    border: 1px solid rgba(#373737, 0.35);
  }
  /* check radio icon */
  .radio-custom input[type=radio]:checked + label:after,
  .radio-custom input[type=checkbox]:checked + label:after {
    content: "";
    position: absolute;
    top: 7px;
    left: 7px;
    border-radius: 64px;
    width: 7px;
    height: 7px;
    color: #8BDBED;
    background-color: #373737;
  }

.checkbox-custom {
	margin-bottom: 7px;
}
  .checkbox-custom label {
    cursor: pointer;
    position: relative;
    padding-left: 32px;
    margin-right: 15px;
    margin-bottom: 0px;
    font-weight: normal;
  }
  .checkbox-custom.form-group label {
    text-transform: none;
  }
  .checkbox-custom.checkbox-disabled label {
    color :#CCC;
  }
  /* checkbox border */
  .checkbox-custom label:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 22px;
    height: 21px;
    background-color: #FFF;
    border: 1px solid rgba(#373737, 0.35);
  }
  .checkbox-custom:hover label:before {
    border: 1px solid rgba(#373737, 0.35);
  }
  /* checked state icon */
  .checkbox-custom input[type=checkbox]:checked + label:after,
  .checkbox-custom input[type=radio]:checked + label:after {
    position: absolute;
    font-family: 'fontello';
    content: "\e816";
    font-size: 13px;
    top: 2px;
    font-weight: bold;
    left: 2px;
    width: 0;
    height: 0;
    font-weight: light;
    transform: rotate(-13deg);
    color: #333;
  }
  /* alt style - filled bg ".fill" */
  .checkbox-custom.fill label {
    cursor: pointer;
    position: relative;
    padding-left: 32px;
    margin-right: 15px;
    margin-bottom: 5px;
  }
  .checkbox-custom.fill label:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 2px;
    width: 22px;
    height: 21px;
    background-color: #666;
  }
  .checkbox-custom.fill input[type=checkbox]:checked + label:after,
  .checkbox-custom.fill input[type=radio]:checked + label:after {
    position: absolute;
    font-family: "FontAwesome";
    content: "\f00c";
    font-size: 12px;
    top: 3px;
    left: 3px;
    width: 0;
    height: 0;
    color: #fff !important;
    transform: rotate(-13deg);
  }
  .fileUpload input.upload {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    font-size: 20px;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
  }
  .choose-file {
    position: relative;
  }
  .choose-file input {
    display: block;
    width: 100%;
    height: 39px;
    padding: 11px 12px;
    font-size: 13px;
    line-height: 1.49;
    color: #555555;
    background-color: #ffffff;
    background-image: none;
    border: 1px solid #C1C4C9;
    border-radius: 2px;
    -webkit-transition: border-color ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s;
    transition: border-color ease-in-out .15s;
  }
